export const TEXT_SPEED = 25;

export const isInViewport = (element) => {
  const rect = element.getBoundingClientRect();
  return (
    rect.top >= -rect.height / 2 &&
    rect.left >= -rect.width &&
    rect.bottom <= (window.innerHeight + rect.height / 2 || document.documentElement.clientHeight + rect.height / 2) &&
    rect.right <= (window.innerWidth + rect.width || document.documentElement.clientWidth + rect.width)
  );
}

