import React from 'react';
import ReactDOM from 'react-dom';
import DownloadBlock from './components/downloadblock';
import './css/downloadpage.css'
import { getImg } from './mabinogi3th_characters';

export default class DownloadPage extends React.Component {
    render() {

        document.body.style.overflowX = 'hidden'
        const NameList = {
            eight: "夜葬八荒",
            deer: "蘑菇鹿",
            rice: "小色米",
            train: "苗兒",
            blue: "沁羽藍",
            banana: "牧小蕉",
            seven: "琪稀",
            warm: "一世溫言",
            post: "信箱",
        }

        let chs = []
        for (const [val, key] of Object.entries(NameList)) {
            chs.push(<DownloadBlock characterID={val} key={`${val}_downloadpage_pargent_${key}`}></DownloadBlock>)
        }

        return (
            <div>
                <div id="downloadPage-background"></div>
                <div id="downloadPage-root">
                    <div className="downloadPage-float">
                        <div className='downloadpage-title'>下載區</div>
                        <div className='downloadpage-content'>
                            嚮導公會是2020年12月10日開始，由臺灣代理商遊戲橘子展開的臺灣特別企劃，
                            截至企劃完成的2022年1月，嚮導公會已經經過了三屆小天使的輪替。
                            本企劃則為貝婷的疊字友友們自行發起的企劃，為了感謝各位小天使的付出臨時起意的企劃！
                            經繪師同意，我們將授權繪製之本人以「姓名標示─非商業性─相同方式分享」使用本企劃繪製的角色立繪，
                            意思就是本人要當頭貼也OK不用問喔！！
                        </div>
                        <div className='download-botton Selectable' onClick={() => { window.location = '/'; }}>返回上一頁</div>
                    </div>
                    <div className="downloadPage-float">
                        <div id="downloadPage-character-containers">
                            {chs}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}