import { css } from 'glamor';
import React from 'react';
import pageDatas from '../CharacterPage_data.json'
import { getImg } from '../mabinogi3th_characters';

export default class DownloadBlock extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            privewFaceUrl: ''
        }
    }
    render() {


        let data = pageDatas[0];
        pageDatas.forEach(element => {
            if (element.imgID === this.props.characterID) {
                data = element;
            }
        });

        let downloadLink = []
        for (let index = 0; index < 9; index++) {
            try {
                const element = require('../characters/' + this.props.characterID + '_' + index + '.png');
                downloadLink.push(element.default)
            } catch (error) { }
        }

        let faecpreviewurl = ''
        let downloadBottons = []
        for (let index = 0; index < downloadLink.length; index++) {
            const text = (index === 0) ? "下載" : `差分${index}`
            const donwloadFile = () => {
                var link = document.createElement("a");
                document.body.appendChild(link);
                link.href = downloadLink[index];
                link.download = `${this.props.characterID}_${index}.png`;
                link.click();
            }
            const MosueOver = () => {
                const link = (index === 0) ? '' : downloadLink[index]
                this.setState({ privewFaceUrl: link })
            }

            downloadBottons.push(<div className="download-botton Selectable"
                key={`download_${this.props.characterID}_botton_${text}`}
                onClick={donwloadFile}
                onMouseOver={MosueOver}>{text}</div>)
        }

        let chImage = css({
            backgroundImage: `url(${getImg(data.imgID, 0).url})`,
            '::after': {
                content: '\'\'',
                position: 'absolute',
                width: '30vmin',
                height: '40vmin',
                backgroundRepeat: 'no-repeat',
                backgroundSize: 'contain',
                backgroundPosition: 'left bottom',
                backgroundImage: `url(${this.state.privewFaceUrl})`,
            }
        })

        return (
            <div className="character-Container">
                <div className={`character-preview ${chImage}`}></div>
                <div className='downloadpage-ch-title'>{data.chname}</div>
                <div className='downloadpage-ch-content'>Illustrator:{data.illust}</div>
                <div className="character-download">
                    {downloadBottons}
                </div>
            </div>
        );
    }
}
