import React from 'react';
import { getFrom } from '../mabinogi3th_characters';
import { ExhibitCharacter } from '../components/ExhibitCharacter';
import { css, keyframes } from 'glamor';
import { scrollByPage } from '../index';
import '../css/homepagestyle.css';
import '../css/coverStyle.css'

export class Cover extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      loaded: false,
      mobile: (window.innerWidth < 960),
    }
  }

  componentDidMount() {
    window.addEventListener("load", () => {
      this.setState({
        loaded: true
      })
    })
    window.addEventListener("resize", window => {
      if (window.target.innerWidth < 950 && this.state.mobile === false) {
        this.setState({ mobile: true })
      }
      if (window.target.innerWidth > 950 && this.state.mobile !== false) {
        this.setState({ mobile: false })
      }
    })
  }

  render() {

    let chs = [];
    const NameList = {
      banana: { t: 100, d: 1 },
      blue: { t: -100, d: 7 },
      seven: { t: 100, d: 5 },
      deer: { t: 100, d: 2 },
      eight: { t: 100, d: 4 },
      rice: { t: 100, d: 8 },
      warm: { t: -100, d: 0 },
      train: { t: -100, d: 3 },
      post: { t: -100, d: 6 },
    };
    for (const [key, value] of Object.entries(NameList)) {
      const data = getFrom(key, 0, (this.state.mobile) ? "homepageLayout_MO" : "homepageLayout");
      chs.push(<ExhibitCharacter img={data} key={key} translate={value.t} delay={value.d * 0.15} enable={this.state.loaded}></ExhibitCharacter>);
    }

    const decoration_back_anim = css({ animation: (this.state.loaded) ? `ScaleIn 1s 1.8s forwards` : `none`, });
    const decoration_front_slim_anim = css({ animation: (this.state.loaded) ? `ScaleIn 0.5s 1.5s forwards` : `none` })
    const decoration_front_anim = css({ animation: (this.state.loaded) ? `ScaleIn 0.5s 1.6s forwards` : `none` })
    const preloadMask = {
      position: "fixed",
      top: "0",
      right: "0",
      width: "100%",
      height: "100%",
      backgroundColor: "#FFFFFF",
      backgroundBlendMode: "lighten",
      animation: (this.state.loaded) ? `FadeOut 3s ease forwards` : `none`,
      pointerEvents: "none",
      zIndex: 999
    };
    const nextButton = css({
      width: '10vmin',
      height: '4vmin',
      marginLeft: 'calc( 50% - 5vmin)',
      backgroundImage: 'url(/static/media/arrow.da8841ae.svg)',
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'contain',
      filter: 'drop-shadow(0 0 6px yellow)',
      opacity: '1',
      animation: (this.state.loaded) ? 'NextButton 0.5s cubic-bezier(0, 0, 0.1, 1.01) infinite alternate, FadeIn 1s ease 3.5s forwards' : `none`,
      pointerEvents: (this.state.loaded) ? `all` : "none",
    })

    //Particles===========================================================
    const createParticle = (scale) => {
      const _45 = `${45 * scale}`;
      const _15 = `${15 * scale}`;
      const _30 = `${30 * scale}`;
      const _60 = `${60 * scale}`;
      const _105 = `${105 * scale}`;
      const _90 = `${90 * scale}`;
      const _particleAnim = keyframes({
        '0%': { transform: "scale(0,0)", filter: "brightness(1)" },
        '100%': { transform: "scale(1,1)", filter: "brightness(1.2)" }
      });
      return {
        position: "absolute",
        left: `${Math.random() * 80}%`,
        top: `${Math.random() * 80}%`,
        width: `${58 * scale}px`,
        height: `${90 * scale}px`,
        backgroundColor: "rgb(255, 255, 255)",
        transform: "scale(0,0)",
        clipPath: `path('M 0 ${_45} C ${_15} ${_45} ${_30} ${_30} ${_30} 0 C ${_30} ${_30} ${_45} ${_45} ${_60} ${_45} C ${_30} ${_45} ${_30} ${_90} ${_30} ${_105} C ${_30} ${_90} ${_30} ${_45} 0 ${_45} Z')`,
        animation: `${_particleAnim} ${0.4}s ease-in-out ${Math.random() * 5}s infinite alternate`
      };
    };
    let particles = [];
    let particles_amount = 15 + (Math.random() * 15);
    for (let index = 0; index < particles_amount; index++) {
      particles.push(<div style={createParticle(Math.random() * 0.3 + 0.05)} key={`particles${index}`}></div>);
    }
    const particleWarp = css({
      filter: "drop-shadow(0 0 0.75rem yellow)",
      width: "100%",
      height: "100%",
      overflow: "hidden",
      position: "absolute",
      top: 0,
      pointerEvents: "none"
    });
    //Particles===========================================================

    const pic = css({
      backgroundImage: `url(${require('../img/background/exhibitPage0.png').default})`,
      backgroundRepeat: "no-repeat",
      backgroundColor: `transparent`,
      filter:`blur(2px)`
    });

    return (
      <div className='OnePage'>

        {/* <div className={`infoPictureArea ${pic}`}></div> */}
        <div className='decoration-back' {...decoration_back_anim}></div>
        <div className='character-wrap'>{chs}</div>
        <div className='decoration-front' {...decoration_front_anim}></div>
        <div className='decoration-front-slim' {...decoration_front_slim_anim}></div>

        <div className='copyrightText'>
          本企劃有部分素材源自由韓國devCat所開發的遊戲新瑪奇Mabinogi。<br></br>
          除外則為Mabinogi之中玩家角色的二次創作。<br></br>
          本企劃為貝婷的疊字友友們自行發起的企劃。<br></br>
        </div>

        <div {...particleWarp}>{particles}</div>

        <div className='CoverController'>
          <div className='LogoType'></div>
          <div className='FlexHoverToScale'>
            <div {...nextButton} onClick={() => { scrollByPage(1); }}></div>
          </div>
        </div>

        <div style={preloadMask}></div>

      </div>
    );
  }
}
